<template>
  <div style="height:100%;">
    <el-table :data="gmvrslist">
      <el-table-column prop="version" label="版本号"></el-table-column>
      <el-table-column prop="updateTime" label="更新时间"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      gmvrslist: []
    }
  },
  created() {
    let api = "/api/gameVersions/all";
    this.$http.get(api).then(res => {
      if(!res.data.code) {
        this.gmvrslist = res.data.gmvrslist;
      } else {
        this.$message['error']("查询失败");
      }
    }).catch(err => {
      console.log(err);
      this.$message['error']("网络错误");
    });
  }
}
</script>

<style scoped>
</style>



