<template>
  <div style="height:100%;">
    <el-table :data="levellist">
      <el-table-column prop="levelID" label="id" width="60"></el-table-column>
      <el-table-column label="关卡名">
        <template slot-scope="scope">
          <v-popover placement="bottom" :title="scope.row.levelName" trigger="hover">
            <el-button type="text" @click="toLevelitem(scope.row)">{{scope.row.levelName}}</el-button>
            <div slot="content">
              <p>mapid: {{scope.row.mapMsg.mapID}}</p>
              <p>mapName: {{scope.row.mapMsg.mapName}}</p>
              <p>description: {{scope.row.mapMsg.description}}</p>
            </div>
          </v-popover>
        </template>
      </el-table-column>
      <el-table-column prop="difficulty" label="难度" width="80"></el-table-column>
      <el-table-column prop="time" v-if="$route.name=='userLevels'" label="记录" width="80"></el-table-column>
      <el-table-column label="op">
        <template slot-scope="scope">
          <v-input-number :min="1" :max="1000" v-model="req[scope.$index].time"></v-input-number>
          <el-button type="text" style="margin:0 12px" @click="onRecord(scope.$index, scope.row)">通关</el-button>
        </template>
      </el-table-column>
    </el-table>
    <v-form direction="inline" style="margin:12px">
      <v-form-item label="类型" style="width:120px">
        <v-select :data="options" style="width:56px" v-model="getAward.type"></v-select>
      </v-form-item>
      <v-form-item label="id" v-if="getAward.type != 'goldcoin'" style="width:150px">
        <v-input-number :min="1" :max="1000" v-model="getAward.goodId"></v-input-number>
      </v-form-item>
      <v-form-item label="数量" style="width:150px">
        <v-input-number :min="1" :max="1000" v-model="getAward.goodNum"></v-input-number>
      </v-form-item>
      <v-form-item style="width:100px">
        <v-button type="primary" @click="onAward()">获得</v-button>
      </v-form-item>
    </v-form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      levellist: [],
      req: [],
      getAward: {
        type: "skin",
        goodId: 0,
        goodNum: 0
      },
      options: [{
        value: 'skin',
        label: '皮肤'
      }, {
        value: 'props',
        label: '道具'
      }, {
        value: 'goldcoin',
        label: '金币'
      }]
    }
  },
  watch: {
    '$route' (to, from) {
      let api = "/api/levels";
      if(to.name == 'userLevels') {
        api += "/user/" + this.$store.state.LogMsg.account;
      } else {
        api += "/all";
      }
      this.$http.get(api).then(res => {
        if(!res.data.code) {
          this.levellist = res.data.levellist;
          if(this.levellist == null || this.levellist.length == 0) {
            this.$message['warning']("关卡数量为0");
          }
          this.req.splice(0, this.req.length);
          for(let i in this.levellist){
            let tmpReq = {
              time: 0
            }
            this.req.push(tmpReq);
          }
        } else {
          this.$message['error']("查询失败");
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    }
  },
  created() {
    let api = "/api/levels";
    if(this.$route.name == 'userLevels') {
      api += "/user/" + this.$store.state.LogMsg.account;
    } else {
      api += "/all";
    }
    this.$http.get(api).then(res => {
      if(!res.data.code) {
        this.levellist = res.data.levellist;
        if(this.levellist == null || this.levellist.length == 0) {
          this.$message['warning']("关卡数量为0");
        }
        this.req.splice(0, this.req.length);
        for(let i in this.levellist){
          let tmpReq = {
            time: 0
          }
          this.req.push(tmpReq);
        }
      } else {
        this.$message['error']("查询失败");
      }
    }).catch(err => {
      console.log(err);
      this.$message['error']("网络错误");
    });
  },
  methods: {
    onRecord(index, item) {
      let api = "/api/gameRecord/"+this.$store.state.LogMsg.account+"/"+item.levelID;
      if (this.req[index].time == null || this.req[index].time == "" || this.req[index].time == 0) {
        this.$message['error']("通关时间不合法");
      }
      this.$http.post(api, this.req[index]).then(res => {
        switch (res.data.code) {
          case 0:
            this.$message['success']("刷新记录");
          case 1:
            this.$store.commit('goldCoinChange', res.data.levelGold);
            this.req[index].time = 0;
            break;
          default:
            this.$message['error']("通关失败");
        }

      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    },
    onAward() {
      let api = "/api/award/"+this.$store.state.LogMsg.account;
      this.$http.post(api, this.getAward).then(res => {
        if(res.data.code) {
          this.$message['error']("获得道具/皮肤/金币失败");
        } else {
          this.$message['success']("获得道具/皮肤/金币成功");
        }
      }).catch(err => {
        console.log(err);
        this.$message['error']("网络错误");
      });
    },
    toLevelitem(index) {
      this.$router.push({path: '/Level/'+index.levelID});
    }
  }
}
</script>

<style scoped>
</style>



